.container .item4{
    background-color: rgb(141, 137, 137);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.container .item4 .contact-component ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 400px;
}

.container .item4 .contact-component ul li{
    list-style: none;
}

.container .item4 .contact-component ul li a{
    text-decoration: none;
    color: black;
}

.container .item4 .contact-component ul li a .contact-icon{
    margin-right: 5px;
    font-size: 20px;
    cursor: pointer;
    color: black;
}