.container{
    display: grid;
    grid-template-rows: 1fr 4fr 1fr 0.5fr;
    height: 100vh;
}
.container .item1 h3{
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(13, 13, 14);
    font-family: Georgia, 'Times New Roman', Times, serif;
    display: flex;
    flex-wrap: wrap;
}
h3 .ai-icon{
color: rgb(22, 22, 22);
margin-right: 10px;
}
.container .item1{
    background-color: rgb(155, 154, 154);
    height: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.container .item2 p{
    width: 400px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.container .item2{
    display: flex;
    justify-content: center;
    align-items: center;
}

.container .item3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
}

.container .item3 input{
    border: none;
    outline: none;
    width: 600px;
    border-bottom: 1px solid rgb(63, 61, 61);
}

.container .item3 button{
    background-color: blueviolet;
    border: none;
    padding: 3px;
    color: aliceblue;
    font-size: 20px;
    width: 90px;
    border-radius: 2rem;
}